// 数据策略管理接口
import http from '../httpConfig'
const table = 'mrbase_sys_data_strategy_master'


// 列表
export const listData = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/data/permission/list-data-permission/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

